<template>
	<div>
		<form @submit.prevent="onSubmit">
			<input type="text" v-model="title">
			<button class="btn btn-danger" type="submit">Create</button>
		</form>
		<button @click="$emit('convert')" class="btn btn-outline-danger fat">Share</button>
		<br>
		<span>{{ share }}</span>
	</div>
</template>

<script>
	export default {
		data(){
			return {
				title: ""
			}
		},
		props: ['share'],
		methods: {
			onSubmit(){
				if (this.title.trim()) {
					var name, status
					if (/\$.+\$/gm.exec(this.title)) {
						var all = /\$(.+)\$/gm.exec(this.title)[1]
						var words = all.split(",")
						console.log(words)
						for (var i = 0; i < words.length; i++) {
							var nas = /n\s(.+)\ss\s(.+)/gm.exec(words[i])
							console.log(nas)
							name = nas[1]
							status = nas[2] == "true" ? true : false
							const newTodo = {
							id: Date.now()+i,
							name: name,
							status: status
							}
							this.$emit('add-todo', newTodo)
							this.title = ""
							}
							return
					} else {
						const newTodo = {
							id: Date.now(),
							name: this.title,
							status: false
						}
				this.$emit('add-todo', newTodo)
				this.title = ""
					}
				}
			}
		}
	}
</script>

<style scoped>
  form {
    display: flex;
  }
  input {
	border-radius: 5%;
    width: 500px;
  }
  .fat {
	width: 500px;
  }
</style>
